import * as React from "react";
import { useLayoutEffect, useRef } from "react";

export interface HighlightProps {
  children?: React.ReactElement;
}

export const Highlight: React.FunctionComponent<HighlightProps> = ({ children }) => {
  useLayoutEffect(() => {
    function listener() {
      return true;
    }

    document.body.addEventListener("", listener);
    return () => {
      document.body.removeEventListener("", listener);
    };
  }, []);

  return <>{children}</>;
};
